import Exif from 'exif-js'
import Crop from './crop'
import ImageUtil from './image'
const defaultImg = '../../../static/img/upload_photo_bg.png'
export default {
  components: {
    Crop
  },
  props: {
    defaultUrl: {
      type: String,
      default: ''
    }
  },
  watch: {
    defaultUrl() {
      this.setImageUrl()
    }
  },
  mounted() {
    this.setImageUrl()
  },
  data() {
    return {
      destroyInput: false, // input, onchange
      isIos: ImageUtil.isIos, // ios
      imgUrl: '', // 
      filename: '', // 
      Orientation: '', // 
      showCrop: false, // 
      showStatusWrapper: false, // 
      showStatusFail: false, // 
      procent: 0, // 
      showSpan: true
    }
  },
  methods: {
    setImageUrl() {
      this.imgUrl = this.defaultUrl ? this.defaultUrl : defaultImg
    },
    onChange(e) {
      // inputfile
      let files = e.target.files
      let file = e.target.files[0]
      if (files.length > 1) {
        this.utils.msg('1')
        return false
      }
      if (['image/jpg', 'image/jpeg', 'image/png', 'image/gif'].indexOf(file.type) < 0) {
        this.utils.msg('')
        return false
      }
      if (file.size > 10 * 1024 * 1024) {
        this.utils.msg('10M')
        return false
      }
      let url = ImageUtil.getObjectURL(file);
      this.filename = file.name
      this.destroyInput = true
      setTimeout(() => {
        this.destroyInput = false
      }, 20)
      // 
      let t = this
      Exif.getData(file, function() {
        let Orientation = Exif.getTag(file, 'Orientation');
        t.Orientation = Orientation;
        t.initCrop(url)
      })
    },
    initCrop(url) {
      // 
      this.showCrop = true
      this.$refs.cropWrapper.initCrop(url, this.Orientation)
    },
    setUpload(base64) {
      // 
      let t = this
      t.showCrop = false
      t.imgUrl = base64
      t.procent = 0
      t.showStatusFail = false
      t.showStatusWrapper = true
      t.showSpan = false;
      t.upload(base64)
    },
    upload(base64) {
      // 
      let t = this
      let that = this;
      let imgFile = ImageUtil.base64Img2Blog(base64)
      imgFile.lastModifiedDate = new Date()
      ImageUtil.upload({
        file: imgFile,
        filename: t.filename,
        url: '/djjf-web/fileUpload/jdCloud/upload',
        onProgress: function(file, procent) {
          t.procent = procent
        },
        onSuccess: function(file, res) {
          t.showStatusWrapper = false
          t.$emit('uploadSuccess', res)
        },
        onError: function(file, err) {
          t.showStatusFail = true
        }
      })
    }
  }
}



