import Header from '@/pages/common/header'
import Footer from '@/pages/common/footer'
const defaultImg = '../../../static/img/upload_photo_bg.png'
export default {
  name: 'personal',
  components: {
    Header,
    Footer
  },
  data () {
    return {
      defaultIndex: 0,
      certificateList: this.utils.arg.getSelect("professional_certificate"), // 
      showProfessionalCertificate: [], // 
      avatarUrl: '',
      personDataAsset: '',
      productList: [
        {price: '8.7%', prictType: '', state: '', title: '866', type: '', percent: 70, progress: '29873'},
        {price: '1.1230', prictType: '', state: '', title: '866', type: '', percent: 0, progress: ''}
      ],
      loadMoreState: false,
      personData:{},
      cardEntry: this.utils.userInfo.checkUserPowerKey(this.powerKey.cardEntry), // 
      manageAssetListKey: this.utils.userInfo.checkUserPowerKey(this.powerKey.manageAssetListKey), // 
      declarationRecordsKey: this.utils.userInfo.checkUserPowerKey(this.powerKey.declarationRecordsKey), // 
      redeemRecordsListKey: this.utils.userInfo.checkUserPowerKey(this.powerKey.redeemRecordsListKey), // 
      onlineKey: this.utils.userInfo.checkUserPowerKey(this.powerKey.onlineKey), // 
      marketShareKey: this.utils.userInfo.checkUserPowerKey(this.powerKey.marketShareKey),
      onlineStatus: '0',
      fromCancel : false,
      bg:'#04BE02'
    }
  },
  mounted() {
    // 
    //  this.utils.userInfo.checkUserPowerKey(this.powerKey.personalKey)
    if (!this.utils.userInfo.checkUserPowerKey(this.powerKey.messageListKey)) {
      this.func.ajaxPost(this.api.home.queryTradeApply, {}, res => {
        if (res.data.code == 200) {
          let data = res.data.data.records;
          if (data && data.length > 0) {
            this.confirm(data, 0, data.length);
          } else {
            this.querySalesCard();
          }
        }
      })
    }
  },
  methods: {
    confirm(data, i, len) {
      this.$vux.confirm.show({
        title: '',
        confirmText: '',
        cancelText: '',
        content: '<div class="confirm-body"><div class="confirmBody2"><p>' + data[i].productName + '</p><p>' + data[i].customerName + '</p><div class="text-center lightGrey mt10 fw400"></div></div>',
        // show
        onCancel: () => {
          i++
          if (i < len) {
            this.$vux.confirm.hide()
            let that = this;
            setTimeout(function () {
              that.confirm(data, i, len)
            }, 200)

          } else {
            this.$vux.confirm.hide();
            this.querySalesCard();
          }
        },
        onConfirm: () => {
          this.$vux.confirm.hide();
          this.$router.push({name: 'redeem', params: {tradeId: data[i].tradeId, status: data[i].status}})
        }
      })
    },
    // 
    querySalesCard() {
      this.func.ajaxPost(this.api.personal.queryCard,{}, res=>{
        // cardStatus:1,1
        if(res.data.code === 200 && res.data.data.cardStatus !== 1){
          this.remindSalesEditInfo();
        }
      })
    },
    remindSalesEditInfo() {
      this.$vux.confirm.show({
        title: '',
        confirmText: '',
        cancelText: '',
        content: '<div class="confirm-body"><div class="confirmBody2"><div class="text-left lightGrey mt10 fw400 fz12"></div></div></div>',
        // show
        onCancel: () => {
          this.$vux.confirm.hide();
        },
        onConfirm: () => {
          this.$vux.confirm.hide()
          // this.$router.push({name: 'editSalesManBusinessCard'})
          this.$router.push({ path: '/personal/editSalesManBusinessCard'});
        }
      })
    },
    getOnlineStatus(newVal, oldVal) {
      if (newVal){
        this.func.ajaxPost(this.api.personal.updateStatus,{onlineStatus:'1'},res=>{
          if(res.data.code===200){
            this.onlineStatus = '1';
          }
        });
      } else {
        this.$vux.confirm.show({
          title: '',
          confirmText: '',
          cancelText: '',
          content: '<div class="confirm-body"><div class="confirmBody2"></div></div>',
          // show
          onCancel: () => {
            this.$vux.confirm.hide();
            this.onlineStatus = '1';
            return false;
          },
          onConfirm: () => {
            this.$vux.confirm.hide();
            this.func.ajaxPost(this.api.personal.updateStatus,{onlineStatus:'0'},res=>{
              if(res.data.code===200){
                this.onlineStatus = '0';
              }
            });
          }
        })
      }
    },
    goToSalesManBusinessCard() {
      // 
      if (!this.utils.userInfo.checkUserPowerKey(this.powerKey.cardEntry)) {
        return false;
      }
      this.$router.push({ path: '/personal/editSalesManBusinessCard', query:{cardStatus:this.personData.cardStatus}});
    },
  },
  created(){
    this.func.ajaxPost(this.api.personal.queryCard,{},res=>{
      if(res.data.code===200){
        this.personData=res.data.data;
        this.avatarUrl = this.personData.avatar ? this.personData.avatar : '../../../static/img/user.png';
        this.onlineStatus = this.personData.onlineStatus ? this.personData.onlineStatus + '' : '0';
        if(this.personData.professionalCertificate) {
          let arr = this.personData.professionalCertificate.split(',');
          // arr 1,2,3,4
          arr.forEach(item => {
            if(this.utils.ifNumber(item)) {
              for ( let i = 0;i<this.certificateList.length;i++) {
                if (item === this.certificateList[i].key ) {
                  this.showProfessionalCertificate.push(this.certificateList[i]);
                }
              }

            }
          })
        }
      }
    },false);
    this.func.ajaxPost(this.api.personal.Query,{},res=>{
      if(res.data.code===200){
        this.personDataAsset=res.data.data;
      }
    });
  }
}



