export default {
    props: ['index'],
    data() {
        return {
            startX: 0,   //
          startY: 0,   //
            endX: 0,     //
            moveX: 0,   //
          moveY: 0, //
            disX: 0,    //
          disY: 0,    //
            deleteSlider: '',//,v-bind:style="deleteSlider"
        }
    },
    methods:{
        touchStart(ev){
            ev = ev || event
            //tounches1
            if(ev.touches.length == 1){
                // 
                this.startX = ev.touches[0].clientX;
              this.startY = ev.touches[0].clientY;
            }
        },
        touchMove(ev){
            ev = ev || event;
            //
            let wd = this.$refs.remove.offsetWidth;
            if(ev.touches.length == 1) {
                // 
                this.moveX = ev.touches[0].clientX
              this.moveY = ev.touches[0].clientY
                // 
                this.disX = this.startX - this.moveX;
              this.disY = this.startY - this.moveY;
                // 
                if (this.disX < 0 || this.disX == 0 || this.disY !== 0) {
                    this.deleteSlider = "transform:translateX(0px)";
                } else if (this.disX > 0 && this.disY === 0) {// 0
                    // *5
                    this.deleteSlider = "transform:translateX(-" + this.disX*5 + "px)";
                    // 
                    if (this.disX*5 >= wd) {
                        this.deleteSlider = "transform:translateX(-" +wd+ "px)";
                    }
                }
            }
        },
        touchEnd(ev){
            ev = ev || event;
            let wd = this.$refs.remove.offsetWidth;
            if (ev.changedTouches.length == 1) {
                let endX = ev.changedTouches[0].clientX;
              let endY = ev.changedTouches[0].clientY;
                this.disX = this.startX - endX;
              this.disY = this.startY - endY;
                //,
              if(Math.abs(this.disY) > 20) {
                this.deleteSlider = "transform:translateX(0px)";
                return false;
              }
                if ((this.disX*5) < (wd/2)) {
                    this.deleteSlider = "transform:translateX(0px)";
                }else{
                    // 
                    this.deleteSlider = "transform:translateX(-"+wd+ "px)";
                }
            }
        },
        deleteLine (){
            this.deleteSlider = "transform:translateX(0px)";
            this.$emit('deleteLine');
        }
    }
}



