const image = {}
import utils from '@/utils/utils.js'
const UA = navigator.userAgent

const isIpad = /(iPad).*OS\s([\d_]+)/.test(UA)
const isIpod = /(iPod)(.*OS\s([\d_]+))?/.test(UA)
const isIphone = !isIpad && /(iPhone\sOS)\s([\d_]+)/.test(UA)

image.isIos = isIpad || isIpod || isIphone

image.getObjectURL = function(file) {
  // url
  let URL = window.URL || window.webkitURL || window.mozURL;
  let url = URL.createObjectURL(file)
  return url
}
image.base64Img2Blog = function(base64Str) {
  // base64Blob
  // ';base64'
  let parts = base64Str.split(';base64,')
  // 'data:'contentType
  let contentType = parts[0].split(':')[1]
  // atobbase64
  let raw = window.atob(parts[1])
  let rawLength = raw.length
  // 
  let uInt8Array = new Uint8Array(rawLength)
  for (let i = 0; i < rawLength; ++i) {
    uInt8Array[i] = raw.charCodeAt(i)
  }
  return new Blob([uInt8Array], {type: contentType})
}

image.upload = function(options) {
  // 
  const {file, filename, url, onProgress, onError, onSuccess} = options
  let formData = new FormData();
  let xhr = new XMLHttpRequest();
  // file
  formData.append('file', file, filename)
  xhr.onreadystatechange = function () {
    // 
    if (xhr.readyState === 4) {
      if (xhr.status === 200) {
        try {
          const ret = xhr.responseText
          onSuccess(file, ret)
        } catch (err) {
          onError(file, err)
        }
      } else {
        onError(file, new Error('XMLHttpRequest response status is ' + xhr.status))
      }
    }
  }
  xhr.upload.addEventListener('progress', function (evt) {
    // 
    if (evt.total === 0) return
    const percent = Math.floor((evt.loaded / evt.total)*100)
    onProgress(file, percent)
  }, false)
  xhr.open('POST', url);
  xhr.setRequestHeader('token', localStorage.getItem("token"));
  xhr.send(formData)
}

image.compress = function(img, Orientation) {
  // 
  // alert('' + Orientation)
  let canvas = document.createElement('canvas')
  let ctx = canvas.getContext('2d')
  // canvas
  let tCanvas = document.createElement('canvas')
  let tctx = tCanvas.getContext('2d')
  let initSize = img.src.length;
  let width = img.width;
  let height = img.height;


  // 400
  let ratio
  if ((ratio = width * height / 4000000) > 1) {
    ratio = Math.sqrt(ratio)
    width /= ratio
    height /= ratio
  } else {
    ratio = 1
  }
  canvas.width = width
  canvas.height = height
  // 
  ctx.fillStyle = '#fff'
  ctx.fillRect(0, 0, canvas.width, canvas.height)
  // 100
  let count
  if ((count = width * height / 1000000) > 1) {
    count = ~~(Math.sqrt(count) + 1) // 
    // 
    let nw = ~~(width / count)
    let nh = ~~(height / count)
    tCanvas.width = nw
    tCanvas.height = nh
    for (let i = 0; i < count; i++) {
      for (let j = 0; j < count; j++) {
        tctx.drawImage(img, i * nw * ratio, j * nh * ratio, nw * ratio, nh * ratio, 0, 0, nw, nh)
        ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh)
      }
    }
  } else {
    ctx.drawImage(img, 0, 0, width, height)
  }
  // ios 
  if (Orientation && Orientation !== '' && Orientation !== 1) {
    switch (Orientation) {
      case 6: // 90
        // image.rotateImg(img, 'left', canvas)
        break
      case 8: // 90
        // image.rotateImg(img, 'right', canvas)
        break
      case 3: // 180
        // image.rotateImg(img, 'right', canvas) // 
        // image.rotateImg(img, 'right', canvas)
        break
    }
  }
  // jpegs
  let ndata = canvas.toDataURL('image/jpeg', 1)
  tCanvas.width = tCanvas.height = canvas.width = canvas.height = 0
  return ndata
}

image.rotateImg = function(img, direction, canvas) {
  // 
  // 4
  const minStep = 0
  const maxStep = 3
  if (img == null) return
  // imgimg
  let height = img.height
  let width = img.width
  let step = 2
  if (step == null) {
    step = minStep
  }
  if (direction === 'right') {
    step++
    // 
    step > maxStep && (step = minStep)
  } else {
    step--
    step < minStep && (step = maxStep)
  }
  // 
  let degree = step * 90 * Math.PI / 180
  let ctx = canvas.getContext('2d')
  switch (step) {
    case 0:
      canvas.width = width
      canvas.height = height
      ctx.drawImage(img, 0, 0)
      break
    case 1:
      canvas.width = height
      canvas.height = width
      ctx.rotate(degree)
      ctx.drawImage(img, 0, -height)
      break
    case 2:
      canvas.width = width
      canvas.height = height
      ctx.rotate(degree)
      ctx.drawImage(img, -width, -height)
      break
    case 3:
      canvas.width = height
      canvas.height = width
      ctx.rotate(degree)
      ctx.drawImage(img, -width, 0)
      break
  }
}

export default image



