import Header from '@/pages/common/header'
import Footer from '@/pages/common/footer'
export default {
    name: 'questions',
    components: {
        Header,
        Footer
    },
    created(){
       //
       let paperType=this.$route.query.paperType;
       this.func.ajaxPost(this.api.customer.GetRiskPaper, {paperType:paperType}, res => {
        if (res.data.code === 200) {
           this.RiskPaperData=res.data.data;
        } else {
          this.$message.error(res.data.msg)
        }
      })
    },
    data () {
        return {
            step: 1,
            percent: 10,
            total: 10,
            previousState: false,
            nextState: true,
            formData: {},
            RiskPaperData:[],
            listData: [
            {
                title: '1',
                select: [],
                data: [
                {check: false, title: '50'},
                {check: false, title: '50-100'},
                {check: false, title: '100-200'},
                {check: false, title: '200-500'},
                {check: false, title: '500-1000'}
                ]
            },
            {
                title: '2',
                select: [],
                data: [
                {check: false, title: '50'},
                {check: false, title: '50-100'},
                {check: false, title: '100-200'},
                {check: false, title: '200-500'},
                {check: false, title: '500-1000'}
                ]
            },
            {
                title: '3',
                select: [],
                data: [
                {check: false, title: '50'},
                {check: false, title: '50-100'},
                {check: false, title: '100-200'},
                {check: false, title: '200-500'},
                {check: false, title: '500-1000'}
                ]
            },
            {
                title: '4',
                select: [],
                data: [
                {check: false, title: '50'},
                {check: false, title: '50-100'},
                {check: false, title: '100-200'},
                {check: false, title: '200-500'},
                {check: false, title: '500-1000'}
                ]
            },
            {
                title: '5',
                select: [],
                data: [
                {check: false, title: '50'},
                {check: false, title: '50-100'},
                {check: false, title: '100-200'},
                {check: false, title: '200-500'},
                {check: false, title: '500-1000'}
                ]
            },
            {
                title: '6',
                select: [],
                data: [
                {check: false, title: '50'},
                {check: false, title: '50-100'},
                {check: false, title: '100-200'},
                {check: false, title: '200-500'},
                {check: false, title: '500-1000'}
                ]
            },
            {
                title: '7',
                select: [],
                data: [
                {check: false, title: '50'},
                {check: false, title: '50-100'},
                {check: false, title: '100-200'},
                {check: false, title: '200-500'},
                {check: false, title: '500-1000'}
                ]
            },
            {
                title: '8',
                select: [],
                data: [
                {check: false, title: '50'},
                {check: false, title: '50-100'},
                {check: false, title: '100-200'},
                {check: false, title: '200-500'},
                {check: false, title: '500-1000'}
                ]
            },
            {
                title: '9',
                select: [],
                data: [
                {check: false, title: '50'},
                {check: false, title: '50-100'},
                {check: false, title: '100-200'},
                {check: false, title: '200-500'},
                {check: false, title: '500-1000'}
                ]
            },
            {
                title: '10',
                select: [],
                data: [
                {check: false, title: '50'},
                {check: false, title: '50-100'},
                {check: false, title: '100-200'},
                {check: false, title: '200-500'},
                {check: false, title: '500-1000'}
                ]
            }
            ]
        }
    },
    mounted(){
        //
        this.init()
    },
    methods: {
        //
        init(){
            this.select(1)
            this.total = this.listData.length
        },
        //
        select(step, type){
            if(!type && step != 1 && this.formData.select && this.formData.select.length == 0){
               this.utils.alert('')
               return 
            }
            this.step = step
            this.percent = step / this.total * 100
            this.formData = this.listData[step-1]
        },
        //
        previous(){
            this.nextState = true
            if(this.step > 1){
                this.select(this.step - 1, true)
            }else{
                this.previousState = false
                this.utils.msg('')
            }
        },
        //
        next(){
            this.previousState = true
            if(this.step < this.total){
                this.select(this.step + 1)
            }else{
                this.nextState = false
                this.utils.msg('')
            }
        },
        //
        checkerBox(index){
            let check = this.formData.data[index].check
            this.formData.data[index].check = !check
            if(!check){
                this.formData.select.push(1)
            }else{
                this.formData.select.splice(index, 1)
            }
        },
        //
        save(){
            this.select(this.total) 
            if(this.formData.select && this.formData.select.length == 0){
               this.utils.alert('')
               return 
            }
           this.utils.goToUrl('/customer/evaluation')
        }
    }
}



