import Cropper from 'cropperjs'
import ImageUtil from './image'
export default {
  data() {
    return {
      cropWrapper: null, // 
      previewImageEl: null, // 
      previewImageUrl: '', // 
      Orientation: '', // 
      fakeLoading: true,
      bigBody: '',
      loadingDiv: ''
    }
  },
  methods: {
    // 
    cancel() {
      this.removeCrop()
    },
    // 
    confirm() {
      this.startCrop()
    },
    initCrop(url, Orientation) {
      // 
      // 
      this.Orientation = Orientation;
      let t = this;
      if (!this.cropper) {
        let body = document.getElementsByTagName('body')[0]
        this.cropWrapper = document.getElementById('cropWrapper');
        this.previewImageEl = document.getElementById('previewImage');
        body.appendChild(this.cropWrapper)
        this.cropper = new Cropper(this.previewImageEl, {
          aspectRatio:  1, // 
          autoCropArea:  0, // 
          viewMode: 1, // canvas
          dragMode: 'move', // 
          guides: true, // 
          cropBoxResizable: true, // 
          cropBoxMovable: true, // 
          dragCrop: true,  // 
          background: false, // 
          checkOrientation: true, // Exif
          checkCrossOrigin: true, // 
          zoomable: true, // 
          zoomOnWheel: false, // 
          center: false, // 
          toggleDragModeOnDblclick: false // cropmove
          // ready: function () {
          //   if (aspectRatio === 'Free') {
          //     let cropBox = t.cropper.cropBox
          //     cropBox.querySelector('span.cropper-view-box').style.outline = 'none'
          //     t.cropper.disable()
          //   }
          // }
        })
      }
      this.previewImageUrl = url;
      this.cropper.replace(url) // srccropper
    },
    creatLoading() {
      // Loading
      this.bigBody = document.getElementsByTagName('body')[0];
      this.loadingDiv = document.createElement('div');
      let img1 = document.createElement('img');
      this.loadingDiv.setAttribute('id', 'fakeLoading');
      img1.setAttribute('src', '../../../static/img/logoLoading.gif');
      this.loadingDiv.appendChild(img1);
      this.bigBody.appendChild(this.loadingDiv);
    },
    startCrop() {
       // 
      let t = this
      this.creatLoading();
      setTimeout(function () {
        // ,im
        let croppedCanvas = t.cropper.getCroppedCanvas();
        // 
        let roundedCanvas = t.getRoundedCanvas(croppedCanvas);
        // canvastoDataURL()dataURL(base64)
        let base64 = roundedCanvas.toDataURL();
        t.compress(base64)
      }, 20)
    },
    compress(base64) {
      // 
      let t = this;
      let image = new Image();
      image.src = base64;
      // 
      image.onload = () => {
        this.fakeLoading = true;
        let data = ImageUtil.compress(image, this.Orientation);
        this.fakeLoading = false;
        // Loading
        this.bigBody.removeChild(this.loadingDiv);
        this.$emit('finish', data);
      }
    },
    getRoundedCanvas(sourceCanvas) {
      // 
      let canvas = document.createElement('canvas')
      let context = canvas.getContext('2d')
      let width = sourceCanvas.width
      let height = sourceCanvas.height
      canvas.width = width
      canvas.height = height
      context.imageSmoothingEnabled = true
      context.drawImage(sourceCanvas, 0, 0, width, height)
      context.globalCompositeOperation = 'destination-in'
      context.beginPath()
      context.rect(0, 0, width, height)
      context.fill()
      return canvas
    },
    removeCrop () {
      // 
      this.$emit('hide')
      this.cropper.destroy()
      this.cropper = null
    }
  }
}



