import Header from '@/pages/common/header'
import Footer from '@/pages/common/footer'
export default {
  name: 'recommondProductInfo',
  components: {
    Header,
    Footer
  },
  data () {
    return {
      shortName: this.$route.query.shortName,
      encodeShortName:'',
      syncFlag: '0',
      recommendReason: '',
      basicInfoHead: [
        {labelName:'', value: '7'},
        {labelName:'', value: '1'},
        ]
    }
  },
  created(){
    this.encodeShortName = decodeURIComponent(this.shortName);
  },
  methods: {
    changeSyncFlag(){
      this.syncFlag = this.syncFlag === '0' ? '1' : '0';
    },
    recommend() {
      if(!this.recommendReason && this.syncFlag === '1'){
        this.$vux.toast.text("");
        return false;
      }
      let param = {
        appId: this.$route.query.appId, // APPID
        dialogId: this.$route.query.dialogId, // ID
        skuId: this.$route.query.skuId , // SKUID
        syncFlag: this.syncFlag, // 
        recommendReason: this.syncFlag === '1' ? this.recommendReason : '', // 
        recommendDays: '7', // 
        productOrder: '1', // 
        userPin:this.$route.query.pinCode,
        companyId:this.$route.query.companyId
      };
      console.log('param', param);
      this.$vux.confirm.show({
        title:'',
        confirmText:'',
        cancelText:'',
        content:'<div class="confirm-body"><div class="confirmBody2"><p>'+this.encodeShortName+'</p></div></div>',
        onConfirm : () => {
          this.func.ajaxPost(this.api.product.recommendProduct, param, res => {
            if (res.data.code === 200) {
              this.$vux.toast.text('')
              let encodePinCode = res.data.data;
              this.$router.push({ path: '/customer/customerTalk',query:{pinCode:encodePinCode, from: 'product'}});
            } else {
              this.$vux.toast.text(res.data.msg);
            }
          })
        }
      })
    }
  }
}



